.backgrounds{
    background-image: url('./img/webDesing-bg.jpg');
    background-size: cover;
    position: relative;
    opacity: 0.5;
    width: 100vw;
    overflow: hidden;
    padding: 3vw;
}

/* ------------- Home ------------------ */
.backgrounds::before, .backgrounds::after{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '';
    background-color: rgb(66, 66, 66);
    opacity: 0.4;
    z-index: -3;
}

.greetings{
    position: relative;
    z-index: 50;
    padding: 25%;
    width: 100%;
}
@media screen and (max-width: 700px){
    .greetings{
        padding: 10%;
        margin-left: -5vw;
        margin-top: 25vh;
    }
    .home{
        height: 100vh;
    }
}
.home, .projects{
    box-sizing: border-box;
    animation-name: hello;
    animation-duration: 500ms;
    animation-fill-mode: forwards;
}
h1{
    font-size: 4em;
}
h1, h2, h3{
    opacity: 1;
    color: rgb(255, 255, 255);
    font-family: 'Montserrat', sans-serif;
}
.home h1{
    font-size: 6vw;
    width: 100%;
}
.home h2{
    font-size: 3em;
}
/* ------------- Home ------------------ */

@keyframes hello{
    from{opacity:0.6;}
    to{opacity: 1;}
}
/* ------------------ Projects ------------------- */
.links{
    padding: 2vw;
    display: flex;
    width: 50%;
}
.links span{
    color: white;
    font-size: 4em;
    justify-content: space-around;
    margin-left: 25px;
    transition: color 250ms;
}
.links span:hover{
    color: rgb(53, 53, 53);
    cursor: pointer;
}

