.desc-element{
    margin-top: 2vh;
}

.project-item + .project-item{
    margin-top: 1.5vw;
}
.project-item a{
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-weight: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2vw;
}
.project-item a:hover{
    color: rgb(11, 185, 40);
}
.project-item p, h3{
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    max-width: 40ch;
}
.img1{
    background-image: url('./img/portfolioImg.png');
}
.img2{
    background-image: url('./img/tiseguridadImg.png');
}
.img3{
    background-image: url('./img/stickserImg.png');
}
.img4{
    background-image: url('./img/fatimasImg.png');
}
.project-item-img{
    -webkit-clip-path: polygon(0 0, 84% 0, 100% 100%, 0% 100%);
    clip-path: polygon(0 0, 84% 0, 100% 100%, 0% 100%);
    width: 25vw;
    height: 100%;
    background-color: #ffe4c4;
    /* border-radius: 20%; */
    /* border: 2px solid #303030; */
    background-size:100%;
    background-position: center;
    background-repeat: no-repeat;
    transition: background-size 0.5s;
}
@media screen and (max-width: 650px){
    .project-item-img{
        background-size: 100% 100%;
    }
}
.project-item-img::before, .project-item-img::after{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '';
    /* background-color: #3a3a3a; */
    opacity: 0.4;
    z-index: -3;
    transition: opacity 400ms;
}
.project-item-img:hover::before, .project-item-img:hover::after{
    opacity: 0;
}
.project-item-img:hover{
    background-size: 110%;
}
.project-item-desc{
    /* background-color: #4e4e68; */
    /* border-radius:5%; */
    width: 60%;
    margin-left: 2vw;
    position: relative;
    top: 0;
    padding: 3%;
}

