/* ------------------ About --------------------- */

.about{
    opacity: 1;
    /* width: 100vw; */
    height: 100vh;
    box-sizing: border-box;
    overflow: auto;
    background-image: url('./img/Optimized-emile-perron-pcBackground.jpg');
    /* background-size: cover; */
    background-repeat: no-repeat;
    background-position: center;
    /* Photo by Ã‰mile Perron on Unsplash */
}

.about > p{
    font-family: 'Montserrat', sans-serif;
    font-size: 2em;
    color: white;
    margin-bottom: 2vh;
    background-color: rgba(25, 25, 112, 0.404);
    text-align: center;
}
.about > span{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5em;
    color: white;
    font-style: italic;
    margin-bottom: 2vh;
    text-align: center;
    margin-left: auto;
    background-color: rgba(25, 25, 112, 0.404);
}
.about > p::before{
    content:' '
}
.about > span::before,.about >span::after{
    content:'~';
}
.skills{
    margin-top: 4vw;
}
.skills > h2{
    font-size: 3em;
}
.skill-item{
    margin: 0.5vw 2vw;
    position: relative;
    text-align: center;
    float: left;
    padding: 1vw 0.5vw;
    overflow: hidden;
    animation-name: items;
    animation-duration: 1000ms;
    animation-fill-mode: forwards;
    bottom: -8vh;
    opacity: 0.2;

}
@keyframes items{
    from{bottom:-8vh; opacity: 0.2;}
    to{bottom:0;opacity: 1;}
}
.skill-item label{
    color: rgb(0, 0, 0);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.5em;
}
.skill-img{
    max-width: 100%;
    max-height: 100%;

    background-size: contain;
    background-repeat: no-repeat;
    width: 7vw;
    height: 7vw;
    background-position: center;
    margin-left: auto;
    margin-right: auto; 
}
.skill-levels-container{
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: space-evenly;
}
.skill-legend{
    display: flex;
}
.skill-levels-container p{
    position: relative;
    text-align: center;
    margin-left: 2vw;
    color: rgb(0, 0, 0);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.skill-levels{
    float: left;
    width: 2em;
    height: 2em;
    background-color: #ffffff;
    text-align: center;
    margin-left: 2vw;
    /* padding: 1.5vw; */
}
.skill-levels span{
    position: relative;
    top: 30%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.basic{
    border-radius: 50%;
    border: var(--border-levels) solid coral;
}
.medium{
    border-radius: 50%;
    border: var(--border-levels) solid #17af38;
}
.advanced{
    border-radius: 50%;
    border: var(--border-levels) solid #1726af;
}
.skill-area{
    --border-levels: 4px;
    padding: 3vw;
    display: flex;
    flex-direction: column;
    background: #C9D6FF;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #e2e2e250, #C9D6FF50);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #E2E2E250, #C9D6FF50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    border: 5px solid white;
    margin: 2vw 0;
}

@media screen and (max-width: 700px){
    .about{
        box-sizing: border-box;
        height: 100vh;
    }
    .skill-item label{
        font-size: 100%;
    }
}



