*{
    margin: 0;
    padding: 0;
}
.normal-way{
    display: flex;
}
.responsive-way{
    display: none;
}
@media screen and (max-width: 900px) {
    .normal-way{
        display: none;
    }    
    .responsive-way{
        display: block;
    }
}
.sidebar{
    top: 0;
    left: 0;
    /* width: 25vw; */
    height: 100vh;
    background: #bdc3c7;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #2c3e50, #bdc3c7);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #2c3e50, #bdc3c7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    overflow: hidden;
    /* display: flex; */
    flex-direction: column;
    align-content: center;
    transition: width 500ms;
    position: sticky;
    z-index: 100;
}

.extends{
    position: absolute;
    z-index: 3;
    margin-left: 1vw;
    color: #ffffff;
    left: 95%;
    font-size: 2.5em;
    cursor: pointer;
    transition: display 1s;
}
.retract{
    position: relative;
    z-index: 20;
    left: 50%;
    color: #ffffff;
    left: 90%;
    font-size: 2.5em;
    cursor: pointer;
}
.Logo{
    position: relative;
    width: 12vw;
    height: 12vw;
    background-image: url('./components/img/myLogoThumb.png');
    background-size: 120%;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    left: 4.5vw;
    top: 20px;
    transition: background-size 500ms;
}
.Logo:hover{
    background-size: 70%;
}

.Menu{
    margin-top: 30px;
    text-align: center;
    justify-content: space-evenly;
    overflow-x: hidden;
}

.Menu-item{
    position: relative;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;
    padding: 30px;
    width: 100%;
    left: -2vw;
    transition: background-color 250ms;

}
.selected{
    background-color: #319bbb;
}
.Menu-item:hover{
    background-color: #2c3d42;
    cursor: pointer;
}

@media screen and (max-width: 900px) {
    .sidebar {
      display: none;
    }
  }